html {
    color: $clr2;
}

li {
    list-style: none;
}

.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;

    .signup-btn {
        margin-top: 2rem;
        align-self: center;
    }
}

.signup-form {
    display: contents;
}

.filter-container {
    // padding: rem(40px);
    box-shadow: 0px 0px 4px 0px #1783ff7d;
    margin: 0 auto;
    width: 90%;
    transition: 0.2s;
    align-items: baseline;
    flex-wrap: wrap;
    

    &:hover {
        box-shadow: 0px 0px 5px 1px #1783ff7d;
    }

    > *:not(.check-box) {
        margin: 0 1.3rem 0;
    }
}

.header-nav {
    display: flex;
    flex-direction: column;
    align-items: center;

    .header-msg {
        display: flex;
        align-items: baseline;
    }
    .header-btns {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    p {
        margin: rem(10px);
    }
}

.toy-card {
    background-color: #e9ddfc !important;
    transition: background-color 0.2s !important;
    width: 100%;
    justify-self: center;
    transition: 6s;

    &:hover {
        box-shadow: 0px 0px 2px 1px #72b4ff8e;
    }

    .delete-card {
        color: #ff3c0060;
        transition: 0.2s;

        &:hover {
            color: #ff0000ce;
            background-color: #1783ff09;
        }
    }
}

.toy-prev-btns {
    .btn-link {
        width: 30%;
        height: 20%;
        margin: 0.6rem;
        padding: 0px;
        border-radius: 1%;

        a {
            color: gray;
            transition: 0.2s;
        }

        &:hover {
            background-color: #0077ff00;
        }
    }
}

.btn-link:hover a {
    color: #1783ffbb;
}

.form-filter {
    width: 150px;
}

.toy-details {
    background-image: url("https://media.gettyimages.com/photos/small-teddy-bear-isolated-on-white-picture-id182834193?s=2048x2048");
    // background-repeat: no-repeat;
    background-color: rgba(0, 128, 0, 0.089);
    // background-size: cover;
    background-size: 100% 710px;
    // filter: blur(10px) opacity(0.5);
    // background-color: rgb(255, 226, 226);
    margin: 0 auto 1rem;
    max-width: 90vw;
    max-height: 70vh;
    // height: 0vh;
    display: flex;
    flex-direction: column;

    .toy-detail {
        margin: 0.3rem auto;
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 1px 0px grey;

        h3 {
            text-align: left;
        }
        section {
        }
    }
}

.chat-btn-container {
    // position: fixed;
    // justify-content: center;
    position: relative;
    width: 100%;

    .chat-btn {
        // background-color: hotpink;
        position: absolute;
        right: 2%;
        bottom: 2%;
        padding: 5px;
        transition: color 0.2s;
        cursor: pointer;
        &:hover {
            color: rgba(248, 109, 66, 0.849);
        }
    }
}

.add-review-btn {
    // width: 50vw;
    display: flex;
    justify-content: center;
    margin: 3rem 0 0;

}

.toy-img-container {
    width: 100%;
    // height: 20px;
}

//

.modal-content {
    background: white;
    position: fixed;
    // max-width: 70%;
    min-width: 30%;
    max-width: 60%;
    height: 70%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    right: 1%;
    bottom: 1%;

    .close-modal {
        position: absolute;
        right: 0.3%;
        top: 0.3%;
        // padding: 230px;
    }
}
.hide {
    display: none;
}

.chat-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    // padding: 20px;
    // height: 10;

    .chat-msgs {
        width: 85%;
        // height: 300px;
        margin-top: 15%;
        padding-left: 1%;
        height: 100%;
        overflow-y: scroll;

        li {
            display: flex;
            align-items: baseline;
            margin: 1%;

            h4 {
                margin: 0;
            }

            span {
                margin-left: 0.5rem;
            }
        }
    }

    form {
        height: 30%;
        display: flex;
        justify-self: flex-end;
        align-items: flex-end;
        margin: 0 auto;
        justify-content: center;

        * {
            flex-grow: 1;
            padding: 5px;
            height: 30px;
            // vertical-align: baseline;
            margin: 2px 2px 6% 2px;
        }
    }
    .typing-msg {
        position: absolute;
        top: 0;
    }
}

.sign-up-btn-container {
    width: 100%;
    display: flex;
    
    justify-content: center;

}

@media only screen and (max-width: 500px) {
    .filter-container {
        padding-bottom: 45%;

         * {
            // margin-top: 10px;
            padding-top: 5%;
        }
        
        
    }
}