body {
  margin: 0;
  background-color: rgb(255 0 0 / 0.01);
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

/* components */

.container {
  width: 90vw;
  text-align: center;
  margin: 0 auto;
}

.main-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 200px;
  background-color: rgb(23, 131, 255);
}



/* ToyApp */

.toy-list {
  display: grid;
  margin: 3rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: auto;
  column-gap: 3rem;
  row-gap: 3rem;
}

.toy-preview.toy-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 50px;
  padding: 1rem;
  max-width: 300px;
  word-wrap: break-word;
}

.main-control-container {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.filter-container {
  display: flex;
  max-height: 100px;
  justify-content: space-evenly;
  /* align-items: center; */
}
.filter-container .check-box {
  display: inline;
}

.toy-preview *:not(img) {
  /* padding-bottom: 1rem; */
}
.toy-preview {

}

.toy-preview img {
  max-width: 100%;
  object-fit: contain;
}

.toy-preview .remove-toy-btn {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 1%;
  right: 1.5%;
  /* margin: 0.5rem auto; */
}

.toy-prev-btns {
  display: flex;
  /* width: 100%; */
  justify-content: center;
}

.toy-prev-btns > * {
  margin: 0.8rem
}
.toy-prev-btns .btn-link {
  background-color: rgba(255, 255, 255, 0);
  border-radius: 0%;
  width: 20%;
  box-shadow: none;
}

.toy-prev-btns .btn-link:hover {
  
}


/* edit toy */

.edit-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}

.edit-form > div {
  /* margin: 20px auto; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-content: center;
  padding: 10px;
  max-height: 80px;
  margin: 10px 0;
  width: 300px;
}

.edit-form div *:not(label)  {
  /* flex-basis: 70%; */
  /* min-width: 100px; */
}

.edit-form div .name-input, .edit-form div .edit-input {
  margin: 0 auto;
  width: 100%;
}

button.save-toy-btn {
  margin: 30px auto;
  max-width: 100px;
  background-color: #ffa217;
}

button.save-toy-btn:hover {
  background-color: #fdaa2e;
}


.add-link {
  width: 10%;
  margin: 10px auto !important;
}

.check-box {
  align-items: baseline;
}


